<template>
<div>
        <v-card elevation="8" outlined tile class="mx-auto my-12"
    max-width="674">
    <v-card-title>Bull's Eye</v-card-title>
    <v-row v-show="false">
        <v-col cols="12">
            <v-text-field v-model="eventStatement" label="Statement"></v-text-field>
        </v-col>
    </v-row>
    <v-row>
        <v-col cols="6">
            <v-datetime-picker label="Date" no-title v-model="startTime"> </v-datetime-picker>
        </v-col>
        <v-col cols="6"  v-show="false">
            <v-text-field label="Number of Days" no-title v-model="numDays"></v-text-field>
        </v-col>
        <v-col cols="6" v-show="false">
            <v-datetime-picker label="End Time" no-title v-model="endTime"> </v-datetime-picker>
        </v-col>
    </v-row>
    <v-row>
        <v-col>
            <v-btn color="primary" @click="createBullsEye">Create Event</v-btn>
        </v-col>
    </v-row>
    <v-row>
        <v-col>
            <v-btn color="primary" @click="fetchBullsEye">Fetch Event</v-btn>
        </v-col>
    </v-row>
    <v-row>
        <v-col>
            <v-text-field v-model="outcomeValue" label="Event Outcome Value"></v-text-field>
        </v-col>
        <v-col>
            <v-btn color="primary" @click="resolveBullsEye">Resolve Event</v-btn>
        </v-col>
    </v-row>
    <v-row>
        <v-col>
            <v-text-field v-model="approveSpenderAccount" label="Approve Delphi Spender"></v-text-field>
        </v-col>
        <v-col>
            <v-text-field v-model="approveSpenderAmount" label="Approve Delphi Amount"></v-text-field>
        </v-col>
        <v-col>
            <v-btn color="primary" @click="approve">Approve</v-btn>
        </v-col>
    </v-row>
        </v-card>
</div> 
</template>
<script>
import BigNumber from 'bignumber.js';
import BullsEye from "../lib/contracts/bullsEye";
import DelphiERC20 from "../lib/contracts/delphiERC20";
import {mapGetters, mapMutations} from 'vuex';
import moment from 'moment';
import nse_holidays from "../chains/nse_holidays";
import Vue from 'vue'
import DatetimePicker from 'vuetify-datetime-picker'

Vue.use(DatetimePicker);
export default {
    name: 'BullsEyeCreate',
    mounted: async function(){
        await this.init();
    },
    computed: {
    ...mapGetters([
        "network",
        "getSelectedInstrument",
        "account",
        "loading",
        "balance"
    ]),
    
    },
    data: function(){
        return {
            client: {},
            delphiClient: {},
            eventStatement: '',
            startTime: '',
            endTime: '',
            bullsEyeOracle: '0x0000000000000000000000000000000000000000',
            eventUrl: '',
            outcomeValue: '',
            numDays: 1,
            approveSpenderAccount: '',
            approveSpenderAmount: '',
        }
    },
    methods: {
        init: async function(){
            console.log(this.network);
            this.client = new BullsEye({
                network: this.network,
                instrument: this.getSelectedInstrument,
            });
            this.delphiClient = new DelphiERC20({
                network: this.network,
                instrument: this.getSelectedInstrument,
            });
            await this.client.init();
            await this.delphiClient.init();
            console.log("BullsEyeCreate init")
            const totalSupply = await this.delphiClient.signerContract.totalSupply();
            console.log("totalSupply", totalSupply.toString());
        },
        createBullsEye: async function(){
            const bandsAndPayouts = [10000, 20000, 40000, 80000, 4800, 2400, 1200, 600];
            const bands = [10000, 20000, 40000, 80000];
            const payouts = [4800, 2400, 1200, 600];
            const createCommissionBasisPoints = 0;
            const resolution = 1;
            const realMoney = false;
            //let time = moment(this.startTime.getTime()).utc();
            let time = moment().add(2, "days").utc();
            for (let i=0; i<this.numDays; i++){
                const startTime = moment(time).startOf("day").add(3, "hours").add(30, "minutes");
                if (this.isWeekend(startTime) || this.isHoliday(startTime)){
                    alert("Trading Holiday !");
                    return;
                }
                const endTime = moment(time).startOf("day").add(10, "hours");
                time = time.add(1, 'day');
                const withSigner = this.client.signerContract.connect(this.client.metamaskProvider.getSigner());
                console.log('key', startTime.unix(), endTime.unix(), 'NSE-' + startTime.unix());
                await withSigner.createEvent(startTime.unix(), endTime.unix(), 'NSE-' + startTime.unix(), bandsAndPayouts, createCommissionBasisPoints, resolution, this.bullsEyeOracle,realMoney, {gasLimit: 500000});
            }
           
        },
        fetchBullsEye: async function(){
            let time = moment().add(2, "days").utc();
            const startInSecond = moment(time).startOf("day").add(3, "hours").add(30, "minutes").unix();
            const endInSecond = moment(time).startOf("day").add(10, "hours").unix();
            console.log(startInSecond, endInSecond, 'NSE-' + startInSecond, this.bullsEyeOracle);
            const eventID = await this.client.signerContract.getEventID(startInSecond, endInSecond, this.client.getCurrentAccount(), this.bullsEyeOracle);
            console.log("eventID", eventID);
            const event = await this.client.signerContract.bullsEyeEvents(eventID);
            console.log(event);
            const market = await this.client.signerContract.market(eventID);
            const totalPlayers = market.totalPlayers;
            console.log(market);
            for (let i=0; i<totalPlayers; i++){
                const player = await this.client.signerContract.players(eventID, i);
                const playerPrediction = await this.client.signerContract.playerPredictions(eventID, player, 0);
                const wagerAmount = new BigNumber(playerPrediction.wagerAmount.toString());
                console.log(player, playerPrediction.prediction.toString(), wagerAmount.shiftedBy(-18).toString());
            }
        },
        resolveBullsEye: async function(){
            //let time = moment(this.startTime.getTime()).utc();
            //const startInSecond = moment(time).startOf("day").add(3, "hours").add(30, "minutes").unix();
            //const endInSecond = moment(time).startOf("day").add(10, "hours").unix();
            const eventID = ''; //await this.client.signerContract.getEventID(startInSecond, endInSecond, 'NSE-' + startInSecond, this.bullsEyeOracle);
            const distributePlayerWins = true;
            const withSigner = this.client.signerContract.connect(this.client.metamaskProvider.getSigner());
            console.log(eventID);
            console.log(withSigner);
            await withSigner.resolveEvent(eventID, this.outcomeValue, distributePlayerWins, {gasLimit: 2000000});
        
        },
        isWeekend: function(local){
          const localDateTime = local.format("dddd DD-MMM-YYYY HH:mm:ss");
          const tokens = localDateTime.split(" ");
          if ("Saturday" == tokens[0] || "Sunday" == tokens[0]) return true;
          else return false;
        },
        isHoliday: function(local){
          const localDateTime = local.format("dddd DD-MMM-YYYY HH:mm:ss");
          const tokens = localDateTime.split(" ");
          const date = tokens[1];
          const dateTokens = date.split("-");
          const holidayList = nse_holidays[dateTokens[2]];
          return (holidayList.indexOf(date) > -1);
        },
        approve: async function(){
            const withSigner = this.delphiClient.signerContract.connect(this.delphiClient.metamaskProvider.getSigner());
            await withSigner.approve(this.approveSpenderAccount, this.approveSpenderAmount, {gasLimit: 2000000});
        }
    }
}
</script>

    